.parentBox{
    height: 10vh;
    width: 100%;
    position: relative;
}


.box1{
    display: flex;
    justify-content: space-evenly;
    background-color: rgba(0, 0, 0, 0.283);
    align-items: center;
    height: 100%;
    width: 100%;
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    position: absolute;
    
    
}

.box2{
    background-image: url(C:/Users/prabe/OneDrive/Desktop/personalwebsite/personalwebsite/src/assets/milky.jpg);
    height: 100%;
    width: 100%;
    position: absolute;
    object-fit:contain;
    image-orientation: revert;
}

.img{
    height: 40%;
    width: 3%;
    object-fit:contain;
    margin-bottom: 1.2%;
    
}

.img:hover{
    cursor: pointer;
    object-fit: contain;
    
    
}

.imgJS{
    height: 40%;
    width: 3%;
    object-fit:contain;
    margin-bottom: 1.2%;

}

.imgJS:hover{
    object-fit: cover;
 
    cursor: pointer;
   
    

}

.img .tooltiptext{
    visibility: hidden;
    height: 40%;
    width: 100px;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    text-align: center;
    border-radius: 6px;
    margin-left: 0.5%;
    position: absolute;
    z-index: 1;
    
    
}

.img .tooltiptext::after{
    content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent rgb(255, 255, 255) transparent transparent;
}

.img:hover .tooltiptext{
    visibility: visible;
}

.imgJS .tooltiptext{
    visibility: hidden;
    height: 40%;
    width: 100px;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    text-align: center;
    border-radius: 6px;
    margin-left: 0.5%;
    position: absolute;
    z-index: 1;
    
    
}

.imgJS .tooltiptext::after{
    content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent rgb(255, 255, 255) transparent transparent;
}

.imgJS:hover .tooltiptext{
    visibility: visible;
}

